
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline
}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block
}
body{line-height:1
}
ol,ul{list-style:none
}
blockquote,q{quotes:none
}
blockquote:before,blockquote:after,q:before,q:after{content:'';content:none
}
table{border-collapse:collapse;border-spacing:0
}
.clearfix{clear:both;overflow:hidden
}
.clearfix2:before,.clearfix2:after{content:"";display:table
}
.clearfix2:after{clear:both
}
.clearfix2{zoom:1
}
body{font-family:'Open Sans', sans-serif;background:#e5e5e5
}
body a{text-decoration:none;color:#fff
}
body a:hover{text-decoration:underline
}
body header{height:100px;line-height:100px;background:#303e49
}
body header h1{font-size:2rem;text-align:center;color:#fff
}
body.cssanimations .b-timeline_img.is-hidden{visibility:hidden
}
body.cssanimations .b-timeline_img.bounce-in{visibility:visible;animation:cd-bounce-1 0.6s
}
body.cssanimations .b-timeline_content.is-hidden{visibility:hidden
}
body.cssanimations .b-timeline_content.bounce-in{visibility:visible;animation:cd-bounce-2 0.6s
}
@media only screen and (min-width: 1170px){
body.cssanimations .b-timeline:nth-child(even) .b-timeline_content.bounce-in{animation:cd-bounce-2-inverse 0.6s
}
}
@media only screen and (min-width: 1170px){
body.cssanimations .b-timeline_content.bounce-in{visibility:visible;animation:cd-bounce-2 0.6s
}
}
@keyframes cd-bounce-1{
0%{opacity:0;transform:scale(0.5)
}
60%{opacity:1;transform:scale(1.2)
}
100%{transform:scale(1)
}
}
@keyframes cd-bounce-2{
0%{opacity:0;-ms-transform:translateX(-100px);transform:translateX(-100px)
}
60%{opacity:1;-ms-transform:translateX(20px);transform:translateX(20px)
}
100%{-ms-transform:translateX(0);transform:translateX(0)
}
}
@keyframes cd-bounce-2-inverse{
0%{opacity:0;-ms-transform:translateX(100px);transform:translateX(100px)
}
60%{opacity:1;-ms-transform:translateX(-20px);transform:translateX(-20px)
}
100%{-ms-transform:translateX(0);transform:translateX(0)
}
}
body .container{position:relative;margin:3em 2em
}
body .container:before{content:'';position:absolute;top:0;left:28px;height:100%;width:4px;background:#8AB3AD
}
@media (min-width: 400px){
body .container{margin:1.5em 1em
}
}
@media (min-width: 1170px){
body .container::before{left:50%;margin-left:-2px
}
}
body .container .b-timeline{position:relative;margin:2em 0
}
body .container .b-timeline .b-timeline_content{position:relative;margin-left:70px;border:2px solid #ACB7C0;border-radius:4px;padding:20px;background:#f4f4f4
}
body .container .b-timeline .b-timeline_content h2{font-weight:700
}
body .container .b-timeline .b-timeline_content p,body .container .b-timeline .b-timeline_content .read-more,body .container .b-timeline .b-timeline_content .date{font-size:13px
}
body .container .b-timeline .b-timeline_content .read-more,body .container .b-timeline .b-timeline_content .date{display:inline-block
}
body .container .b-timeline .b-timeline_content p{margin:1em 0;line-height:1.6
}
body .container .b-timeline .b-timeline_content .read-more{float:right;padding:.8em 1em;background:#4fb5e1;border:2px solid #4084d3;border-radius:5px
}
body .container .b-timeline .b-timeline_content .date{float:left;padding:.8em 0;opacity:.7
}
body .container .b-timeline .b-timeline_content img{max-width:100%;max-height:950px;display:block
}
body .container .b-timeline .b-timeline_content:before{content:'';position:absolute;top:33px;left:-15px;height:0;width:0;border:7px solid transparent;border-right:7px solid #ACB7C0
}
@media (min-width: 1170px){
body .container .b-timeline .b-timeline_content{margin-left:0;padding:1.6em;width:40.5%
}
body .container .b-timeline .b-timeline_content:before{top:24px;left:100%;border-color:transparent;border-left-color:#ACB7C0
}
body .container .b-timeline .b-timeline_content .date{position:absolute;width:100%;left:122%;top:-5px;font-size:18px
}
}
@media (min-width: 1500px){
body .container .b-timeline .b-timeline_content{width:42.5%
}
}
@media (min-width: 1600px){
body .container .b-timeline .b-timeline_content{width:44.5%
}
}
@media (min-width: 1170px){
body .container .b-timeline:first-child{margin-top:0;margin-bottom:0
}
body .container .b-timeline:last-child{margin-top:0
}
body .container .b-timeline:nth-child(even) .b-timeline_content{float:right
}
body .container .b-timeline:nth-child(even) .b-timeline_content::before{top:24px;left:auto;right:100%;border-color:transparent;border-right-color:#ACB7C0
}
body .container .b-timeline:nth-child(even) .b-timeline_content .read-more{float:right
}
body .container .b-timeline:nth-child(even) .b-timeline_content .date{left:auto;right:122%;text-align:right
}
}



